@import url('https://fonts.googleapis.com/css?family=Lato:400,700');
 
html {
	display: grid;
	min-height: 100%;
}

#Container {
	position: relative;
	margin: 12% auto;
	width: 620px;
	height: 450px;
}
.contacts {
	position: absolute;
	background: #212121;
	opacity: 0.5;
	width: 30%;
	height: 70%;
	top: 2.5%;
	left: 5%;
	border-radius: 10px 0 0 10px;
	box-shadow: -5px 5px 10px rgba(119, 119, 119, .5);
}
.people {
	position: absolute;
	list-style-type: none;
	width: 30.2%;
	left: -10px;
	top: 24.7%;
	line-height: 0.7em;
}
.person {
	padding: 12px 0 12px 12px;
	border-bottom: 1px solid #79c7c5;
	cursor: pointer none;
}
 .people .title {
	text-transform: uppercase;
    font-size: 0.7em;
    margin-left: 14px;
    letter-spacing: 1px;
	color: white;
}
.person:hover {
	background: #212121;
	transition: all 0.3s ease-in-out;
}
.focus {
	background: #212121;
	margin-left: 1px;
}

#Chatbox {
	position: absolute;
	left: 35%;
	height: 75%;
	width: 60%;
	border-radius: 10px;
	box-shadow: 5px 5px 15px rgba(119, 119, 119, 0.5);
}
#TopBar {
	width: 100%;
	height: 60px;
	background: #212121;
	border-radius: 10px 10px 0 0;
}
.avatar {
	width: 35px;
	height: 35px;
	background: linear-gradient(to bottom left, #79c7c5 20%, #a1e2d9 100%);
	border-radius: 50%;
	position: absolute;
	top: 11px;
	left: 15px;
}
.avatar p {
	color: #f9fbff;
	margin: 7px 12px;
}
.name {
	position: absolute;
	top: 22px;
	text-transform: uppercase;
	color: #777;
	font-size: 0.8em;
	letter-spacing: 2px;
	font-weight: 500;
	left: 60px;
}
.menu {
	position: absolute;
	right: 10px;
	top: 20px;
	width: 10px;
	height: 20px;
	cursor: pointer;
}
.menu:hover {
	transform: scale(1.1);
	transition: all 0.3s ease-in;
}

.dots {
	width: 4px;
	height: 4px;
	border-radius: 50%;
	background-color: #79c7c5;
	box-shadow: 0px 7px 0px #79c7c5, 0px 14px 0px #79c7c5;
}
#MessageBody {
	position: absolute;
	background: #212121;
	width: 100%;
	opacity: 0.85;
	top: 60px;
	height: 66%;
    overflow:scroll;
    overflow-x:hidden;
}
.bubble {
	position: relative;
	display: inline-block;
	margin-bottom: 5px;
	color: #f9fbff;
	font-size: 0.7em;
	padding: 10px 10px 10px 12px;
	border-radius: 20px;
}
.incoming {
	position: relative;
	margin-left: 20px;
	margin-top: 10px;
}
.incoming .bubble {
	background: #b2b2b2;
	color: #212121;
	float: left;
}
.incoming-typing {
	position: relative;
	margin-left: 20px;
	margin-top: 10px;
} 
.incoming-typing .bubble {
	background: #eaeaea;
	padding: 8px 13px 9px 13px;
	float: left;
}
.outgoing {
	position: relative;
	margin-right: 20px;
	margin-top: 20px;
}
 .outgoing .bubble {
	 background: #79c7c5;
	 color: #212121;
	 float: right;
}
.outgoing-typing {
	position: relative;
	margin-left: 20px;
	margin-top: 10px;
} 
.outgoing-typing .bubble {
	background: #79c7c5;
	padding: 8px 13px 9px 13px;
	float: right;
}

.ellipsis {
	width: 5px;
	height: 5px;
	display: inline-block;
	background: #212121;
	border-radius: 50%;
	animation: bounce 1.3s linear infinite;
}
.one {
	animation-delay: 0.6s;
	margin: 2px;
}
.two {
	animation-delay: 0.5s;
	margin: 2px;
}
.three {
	animation-delay: 0.8s;
	margin: 2px;
}
 #UsrPad {
	 position: absolute;
	 width: 100%;
	 height: 55px;
	 bottom: 0;
	 background: #212121;
	 border-radius: 0 0 10px 10px;
}
 .left {
	 left: 0px;
}
 input {
	 padding: 7px;
	 width: 74%;
	 left: 5%;
	 position: absolute;
	 border: 0;
	 top: 13px;
	 background: #212121;
	 color: #79c7c5;
}
 input::placeholder {
	 color: #a1e2d9;
}
 input:focus {
	 color: #777;
	 outline: 0;
}
 button {
	 position: absolute;
	 border: 0;
	 background-color: #212121;
	 font-size: 1em;
	 color: #a1e2d9;
	 top: 19px;
	 opacity: 0.8;
	 right: 17px;
	 cursor: pointer;
	 outline: 0;
}
 button:hover {
	 transform: scale(1.1);
	 transition: all 0.3s ease-in-out;
	 color: #79c7c5;
}
 footer {
	 position: absolute;
	 bottom: 0;
	 right: 0;
	 text-align: center;
	 font-size: 0.7em;
	 padding: 10px;
}
 footer p {
	 color: #79c7c5;
	 text-transform: uppercase;
	 letter-spacing: 2px;
}
 footer a {
	 color: #f9fbff;
	 text-decoration: none;
}
 footer a:hover {
	 color: #a1e2d9;
}
 @keyframes bounce {
	 30% {
		 transform: translateY(-2px);
	}
	 60% {
		 transform: translateY(0px);
	}
	 80% {
		 transform: translateY(2px);
	}
	 100% {
		 transform: translateY(0px);
		 opacity: 0.5;
	}
}
 